}
.tabBtn button {
  padding: 15px 0 15px 0;
  width: 25%;
  color: #545454;
  background: none;
  border-top: none;
  border-bottom: none;
  border-left: none;
  border-right: 1px solid #aba9a9;
  outline: none;
  background-position: center;
  transition: background 0.7s;
}
.tabBtn button:hover {
  background: #ffcbd4 radial-gradient(circle, transparent 1%, #ffcbd4 1%) center/15000%;
}
.tabBtn button:active {
  color: #1967dc;
  background-color: #792533;
  background-size: 100%;
  transition: background 0s;
}
.tabBtn button.active {
