body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Belinda Regular', 'Droid Sans', 'Helvetica Neue',
    'No. Seven Regular', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, #d53369 0%, #daae51 100%) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* FLEX HELPER */
.d-flex { display: flex; flex-direction: row; }
.d-flexi { display: inline-flex; flex-direction: row; }
.d-flexc { display: flex; flex-direction: column; }
.fp-flex { display: flex; flex-grow: 1; }
.fc-flex { flex-grow: 1; height: 100%;  }
.jc-spaceb { justify-content: space-between; }
.jc-spacee { justify-content: space-evenly; }
.jc-center { justify-content: center; }
.jc-start { justify-content: flex-start; }
.jc-end { justify-content: flex-end; }
.al-center { align-items: center; }
.al-start { align-items: flex-start; }
.al-end { align-items: flex-end; }
.p-relative { position: relative; }
.p-absolute { position: absolute; }
.txt-center { text-align: center; }
.txt-left { text-align: left; }
.txt-right { text-align: right; }
/* FLEX HELPER */

#lumia-container {
  background-color: #cdcdcd;
  background-color: transparent;
  border-radius: 4px;
  box-shadow: 0px 10px 40px #000;
  -webkit-box-shadow: 0px 0px 40px #000;
  -ms-box-shadow: 0px 0px 40px #000;
  -moz-box-shadow: 0px 0px 40px #000;
  -o-box-shadow: 0px 0px 40px #000;
  margin: 0 auto;
  overflow: hidden;
  padding: 20px 0 0 10px;
  height: 95vh;
  width: 380px;
}
#lumia-inset-shading {
  height: 100vh;
  margin: -40px 0 0 -20px;
  padding: 30px 10px 20px 20px;
  -webkit-box-shadow: inset 0px 0px 30px #000;
  -ms-box-shadow: inset 0px 0px 30px #000;
  -moz-box-shadow: inset 0px 0px 30px #000;
  -o-box-shadow: inset 0px 0px 30px #000;
}
#lumia-glass {
  background-color: #000;
  border: 2px solid #444;
  border-radius: 30px;
  overflow: auto;
  padding: 20px 20px 10px 20px;
}
#lumia-speaker {
  background-color: #434343;
  border-radius: 2px;
  height: 6px;
  margin: 0 auto;
  width: 70px;
}
#lumia-branding {
  color: #bdbdbd;
  height: 20px;
  margin: -15px 0 10px 0;
  padding: 0;
  text-align: right;
  text-transform: uppercase;
  width: 100%;
}
#lumia-screen {
  background-color: #161616;
  position: relative;
  overflow: hidden;
}
.lumia-screen {
  height: 85vh;
  width: 325px;
  border-radius: 10px;
}
#lumia-back {
  float: left;
  padding: 6px 0 0 15px;
}
#lumia-back-top {
  background-color: #bdbdbd;
  height: 9px;
  margin: 0 0 -2px 0;
  -webkit-transform: rotate(220deg);
  -moz-transform: rotate(220deg);
  -o-transform: rotate(220deg);
  -ms-transform: rotate(220deg);
  width: 2px;
}
#lumia-back-mid {
  background-color: #bdbdbd;
  height: 2px;
  width: 14px;
}
#lumia-back-bot {
  background-color: #bdbdbd;
  height: 9px;
  margin: -2px 0 0 0;
  -webkit-transform: rotate(320deg);
  -moz-transform: rotate(320deg);
  -o-transform: rotate(320deg);
  -ms-transform: rotate(320deg);
  width: 2px;
}
#lumia-windows-skew div {
  background-color: #bdbdbd;
  float: left;
  height: 10px;
  width: 11px;
}
#lumia-windows-skew .tl {
  margin: 0 0 2px 0;
}
#lumia-windows-skew .tr {
  margin: 0 0 2px 2px;
}
#lumia-windows-skew .bl {
  margin: 0 0 0 0;
}
#lumia-windows-skew .br {
  margin: 0 0 0 2px;
}
#lumia-search {
  float: right;
  padding: 6px 10px 0 0;
}
#lumia-spyglass-head {
  border: 2px solid #bdbdbd;
  border-radius: 6px;
  float: left;
  height: 8px;
  width: 8px;
}
#lumia-spyglass-base {
  background-color: #bdbdbd;
  float: left;
  height: 2px;
  margin: 11px -2px 0 0;
  -webkit-transform: rotate(320deg);
  -moz-transform: rotate(320deg);
  -o-transform: rotate(320deg);
  -ms-transform: rotate(320deg);
  width: 7px;
}
.lumia-button {
  height: 30px;
  margin: 20px 0 0 0;
  width: 30px;
}
#lumia-windows {
  margin: 23px auto;
  perspective: 10px;
  -webkit-perspective: 10;
  -moz-perspective: 10;
}
#lumia-windows-skew {
  transform: rotateY(-8deg);
  -webkit-transform: rotateY(-8deg);
  -moz-transform: rotateY(-8deg);
  -ms-transform: rotateY(-8deg);
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #ddd;
  height: calc(100vh - 50px);
}

.tabs-inner {
  overflow-y: scroll;
}

.tabs-inner, ion-router-outlet {
  height: calc( calc(100vh - 45px) * 3 );
  /* height per pages */
  /* height: calc( calc(100vh - 45px) * 4 ); */
}